.toggleSwitch {
  position: relative;
  width: 5rem;
  height: 3rem; 
  background-color: hsl(0, 0%, 63%);
  border-radius: 100px;
  box-shadow: inset 0 1px hsla(0, 0%, 29%, 0.5), 0 2px hsla(0, 0%, 70%, 0.3);
  cursor: pointer;
  transition: all 0.2s ease;
}

.night-mode .toggleSwitch {
  background-color: var(--color-primary);
  box-shadow: none;
}

.toggleSwitch::after {
  content: "";
  position: absolute;
  height: 2.5rem;
  width: 2.5rem;
  border-radius: 50%;
  top: 50%;
  left: 50%;
  transform: translate(-85%, -50%);
  background-color: white;
  transition: all 0.2s ease;
}

.night-mode .toggleSwitch::after{
  transform: translate(-15%, -50%);
}