.splashScreen {
	position: absolute;
	top: 0;
	left: 0;
	z-index: 900;
  width: 100%;
  height: 100%;
  background-color: hsl(228, 7%, 14%);
  display: flex;
  justify-content: center;
  align-items: center;
	animation: move-aside 0.5s cubic-bezier(0.895, 0.03, 0.685, 0.22) 1.4s forwards;	
}

.icon {
  stroke-dasharray: 1000;
  width: 6.1rem;
  height: 6.1rem;
  fill: transparent;
  stroke: white;
  stroke-width: 0.05px;
  transform-origin: 50% 50%;
  transform: rotate(45deg);
  animation: fill 0.5s ease-out 1.2s forwards, rotate 0.3s linear 1.3s forwards ;
}

:global(#path1), :global(#path2), :global(#path3) {
  stroke-dashoffset: 1000;
}

:global(#path1) {
  animation: draw-out 1s ease-in forwards 0.1s;
}

:global(#path2) {
  animation: draw-out 1s ease-in forwards 0.2s;
}

:global(#path3) {
  animation: draw-out 1s ease-in forwards 0.3s;
}

@keyframes draw-out {
  to {
    stroke-dashoffset: 0;
  }
}

@keyframes fill {
  to {
    fill: white;
  }
}

@keyframes move-aside {
  to {
    transform: translateX(-120%);
  }
}

@keyframes rotate {
  to {
    transform: rotate(0deg);
  }
}