.container {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-bottom: 15rem;
}

.heroContainer {
  position: relative;
  width: 100%;
  height: 100%;
}

.headerLogoAndToggle {
  margin-top: 7.5rem;

  display: flex;
  justify-content: space-between;
  align-items: center;

  @media only screen and (max-width: 55em) {
    margin-top: 10rem;
  }

  @media only screen and (max-width: 24em) {
    margin-top: 6.1rem;
  }
}

