.headingPrimary {
  font-size: 3.9rem;
  color: var(--color-secondary);
  font-weight: 300;
  line-height: 130%;
  white-space: nowrap;
  margin-top: 11.9rem;

  @media only screen and (max-width: 55em) {
    margin-top: 15rem;
    font-size: 3.4rem;
  }

  @media only screen and (max-width: 24em) {
    font-size: 3rem;
    margin-top: 12rem;
  }
}

.highlighter {
  position: relative;

  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    height: 2.1rem;
    width: 100%;
    z-index: -1;
    background-color: hsla(94, 64%, 60%, 0.28);
  }
}


/* -- Toggle Theme Styles -- */

.toggleContainer {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}