* {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
  border-top: 3px solid hsl(94, 64%, 60%);
}

body {
  max-width: 112rem;
  margin: 0 auto;
  background-color: white;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scroll-behavior: smooth;
  background-color: white;
  transition: all 0.2s ease-out;

  /* -- Colors -- */
  --color-primary: hsl(94, 64%, 60%);
  --color-secondary: hsl(228, 7%, 14%);
  --color-secondary-light: hsl(228, 7%, 23%);
  --color-text: hsl(228, 7%, 23%);
  --color-border: hsl(0, 0%, 63%);
  --box-shadow: hsla(0, 0%, 0%, 0.2);  
}

body.night-mode {
  background-color: hsl(228, 7%, 14%);
  /* -- Night Mode Colors -- */
  --color-primary: hsl(94, 64%, 60%);
  --color-secondary: hsl(0, 100%, 100%);
  --color-secondary-light: hsl(0, 100%, 100%);
  --color-text: hsl(228, 7%, 88%);
  --color-border: hsl(0, 100%, 100%);
  --box-shadow: hsla(0, 0%, 0%, 0.2);  
}
/* -- 
----- 81.25em = 1300px
----- 55em = 880px
----- 41.25em = 660px
-- */


/* -- 1300px -- */
@media only screen and (max-width: 81.25em) {
  body {
    padding: 0 5rem;
  }
}


/* -- 880 px -- */
@media only screen and (max-width: 55em) {
  html {
    font-size: 55%;
  }

  body {
    padding: 0 8rem;
  }
}


/* -- 660px -- */
@media only screen and (max-width: 41.25em) {
  html {
    font-size: 50%;
  }

  body {
    padding: 0 5rem;
  }
}

@media only screen and (max-width: 24em) {
  html {
    font-size: 45%;
  }

  body {
    padding: 0 4rem;
  }
}



